import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { ICobroTipo, IDtoCreateMedioDeCobro, IDtoUpdateMedioDeCobro } from '../../models/cobro-tipo';
import { MediosDeCobroService } from '../../service/medios-de-cobro.service';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { IFondo } from 'app/modules/fondos/fondos/models/fondo';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { IAlertConfig } from '@shared/models/alert';
import { finalize, Subject, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { EmpresaService } from '@shared/service/app/empresa.service';

@Component({
    selector: 'app-save-medios-de-cobro',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss'],
})
export class SaveComponent implements OnInit {
    columns = ['descripcion', 'porcentaje', 'fondo']
    form: FormGroup;
    medio_de_cobro: ICobroTipo;
    title: string;
    fondos: IFondo[] = [];
    showAlert = false;
    _alertConfig: IAlertConfig;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    @Output() public creadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    @Output() public editadoExitosamente: EventEmitter<void> = new EventEmitter<void>();

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _mediosDeCobroService: MediosDeCobroService,
        private _empresaService: EmpresaService,
        public alert: AppAlertService,
        public fb: FormBuilder,
        public dialogRef: MatDialogRef<SaveComponent>
    ) {
        this.medio_de_cobro = this.data ? this.data : null;
        this.title = this.medio_de_cobro?.idcobrotipo ? 'Editar Medio de cobro' : 'Nuevo Medio de cobro';
    }

    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });

        // Load empresa data
        this._empresaService.getEmpresa();
        this.newForm();
        this.exist();
        this.subscribeAlert();
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
            next: (config: IAlertConfig) => {
                this.showAlert = true;
                this._alertConfig = {
                    ...config,
                    appearance: 'border',
                };
                let time = 2;
                timer(100, 1500)
                    .pipe(
                        finalize(() => {
                            this.showAlert = false;
                            this._alertConfig = null;
                        }),
                        takeWhile(() => time > 0),
                        takeUntil(this._unsubscribeAll),
                        tap(() => (time -= 1))
                    )
                    .subscribe();
            },
        });
    }

    newForm() {
        this.form = this.fb.group({
            descripcion: new FormControl((this.medio_de_cobro ? this.medio_de_cobro.descripcion : ''), [Validators.required]),
            porcentaje: new FormControl((this.medio_de_cobro ? this.medio_de_cobro.porcentaje : ''), [Validators.required]),
            idfondo: new FormControl((this.medio_de_cobro ? this.medio_de_cobro.idfondo : ''), [Validators.required]),
            activo: new FormControl((this.medio_de_cobro ? this.medio_de_cobro.activo : '')),
            duplica_numero: new FormControl((this.medio_de_cobro ? this.medio_de_cobro.duplica_numero : '')),
            lleva_banco: new FormControl((this.medio_de_cobro ? this.medio_de_cobro.lleva_banco : '')),
            lleva_numero: new FormControl((this.medio_de_cobro ? this.medio_de_cobro.lleva_numero : '')),
            lleva_vencimiento: new FormControl((this.medio_de_cobro ? this.medio_de_cobro.lleva_vencimiento : '')),
            lleva_fecha_emision: new FormControl((this.medio_de_cobro ? this.medio_de_cobro.lleva_fecha_emision : '')),
            lleva_fecha_cobro: new FormControl((this.medio_de_cobro ? this.medio_de_cobro.lleva_fecha_cobro : '')),
            es_pagare: new FormControl((this.medio_de_cobro ? this.medio_de_cobro.es_pagare : '')),
        });
    }

    /**
    * Si existe un item le mando 'true' a fondos y nada a TM para que me traiga todos los fondos al editar.
    * Caso contrario, si se va a crear un nuevo item, llamo a los fondos sin enviarle un parametro asi me trae solo los activos.
    */
    exist() {
        if (this.medio_de_cobro?.idcobrotipo) {
            this.getFondos(true);
        } else {
            this.getFondos();
        }
    }

    /**
     * Esta funcion la utilizamos cuando necesitamos listar los fondos en el saveComponent
     */
    getFondos(option?) {
        const fondos = this._mediosDeCobroService.getFondos(option).subscribe(fondos => { this.fondos = fondos['result']; })
        return fondos;
    }


    onSubmit() {
        if (!this.form.valid) {
            return this.alert.error('Revise Los Datos Ingresados');
        } else {
            let payload: IDtoCreateMedioDeCobro | IDtoUpdateMedioDeCobro = {
                lleva_numero: this.form.get('lleva_numero').value ? 1 : 0, //Se cambian los valores booleanos del formControlCheck por 1 o 0
                lleva_vencimiento: this.form.get('lleva_vencimiento').value ? 1 : 0,
                lleva_fecha_emision: this.form.get('lleva_fecha_emision').value ? 1 : 0,
                lleva_fecha_cobro: this.form.get('lleva_fecha_cobro').value ? 1 : 0,
                es_pagare: this.form.get('es_pagare').value ? 1 : 0,
                duplica_numero: this.form.get('duplica_numero').value ? 1 : 0,
                lleva_banco: this.form.get('lleva_banco').value ? 1 : 0,
                activo: this.form.get('activo').value ? 1 : 0,
                descripcion: this.form.get('descripcion').value,
                porcentaje: this.form.get('porcentaje').value,
                idfondo: this.form.get('idfondo').value
            }

            if (this.medio_de_cobro?.idcobrotipo) {
                this._mediosDeCobroService.updateMedioDeCobro(this.medio_de_cobro.idcobrotipo, payload)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.editadoExitosamente.emit();
                                this.onCloseMenu();
                            }
                        }
                    });
            } else {
                this._mediosDeCobroService.saveMedioDeCobro(payload)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.creadoExitosamente.emit();
                                this.onCloseMenu();
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}